@import url('https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dongle&family=Nanum+Gothic+Coding&family=Ubuntu+Mono&display=swap');

.App {
  text-align: center;
}

html, body {
  background-color: #202124;
  color: white;
  font-family: 'Ubuntu Mono', 'Nanum Gothic Coding', monospace;
}

.wrapper {
  display: flex;
  justify-content: center;
}

.container {
  display: block;
  width: 70.5rem;
}

.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 70.5rem;
  height: 6rem;
  color: white;
}

.title {
  width: 14rem;
  text-align: left;
  font-size: 2rem;
  font-weight: 500;
}

.navigators {
  display: flex;
  justify-content: end;
  width: 51.75rem;
}

.nav-icons {
  display: flex;
  justify-content: space-between;
  width: 4.25rem;
  margin-top: 3rem;
  font-size: 1.6rem;
  cursor: pointer;
  color: #C0C0C0;
}

.panels {
  display: flex;
  justify-content: space-between;
}

.left-panel {
  display: flex;
  justify-content: center;
  width: 14rem;
  height: calc(100vh - 7.5rem);
  text-align: center;
  border-radius: 8px;
  background-color: #353535;
}

.categories {
  display: block;
  width: 14rem;
  padding: 2rem 1.5rem;
  text-align: left;
}

.category {
  display: flex;
  height: 1.5rem;
  line-height: 1.5rem;
  margin-bottom: 0.5rem;
}

.memo-icon {
  margin-right: 0.4rem;
  font-size: 1.4rem;
}

.bg-colors {
  display: grid;
  justify-items: center;
  width: 14rem;
  padding: 2.5rem 1rem;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows : max-content;
  grid-row-gap: 2rem;
  overflow: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.bg-color {
  width: 4rem;
  height: 4rem;
  border: 2px solid #C0C0C0;
  cursor: pointer;
  background-color: #C2E2E8;
}

.category-title {
  text-align: left;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
}
.category-title:hover {
  font-size:1.2rem;
  font-weight: 800;
}

.pages-panel {
  display: flex;
  width: 56.5rem;
  height: calc(100vh - 7.5rem);
}

.pages-grid {
  display: grid;
  justify-items: end;
  width: 56.5rem;
  grid-template-columns: repeat(3, 1fr);  
  grid-auto-rows : max-content;
  grid-row-gap: 3rem;
  overflow: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.pages-grid::-webkit-scrollbar {
  display: none;
}

.page {
  display: block;
  position: relative;
  width: 14rem;
  height: 16rem;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-size: 1.2rem;
  font-weight: bold;
  border-radius: 8px;
}

.page-title {
  width: 10rem;
  margin-left: 2rem;
  margin-top: 4rem;
  font-size: 1.2rem;
  font-weight: bold;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.page-time {
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 1.5rem;
  font-size: 1rem;
  z-index: 10;
}

.page-hover {
  position: absolute;
  width: 13rem;
  height: 13.5rem;
  padding: 0.5rem 0.5rem 2rem 0.5rem;
  text-align: left;
  font-size: 0.9rem;
  border-radius: 8px;
  background-color: rgba(0, 0, 0, 0.9);
  color: #C0C0C0;
  z-index: 20;
}

.hover-delete {
  display: flex;
  justify-content: end;
  margin-bottom: 1.5rem;
  font-size: 1.6rem;
  z-index: 20;
}

.inline-delete-icon {
  width: 1.5rem;
  text-align: center;
  cursor: pointer;
}

.hover-box {
  height: 10rem;
}

.box-content {
  display: -webkit-box;
  -webkit-line-clamp: 10;
  -webkit-box-orient: vertical;
  padding: 0 1rem 0 1rem;
  word-break: break-all;
  word-wrap: break-word;
  overflow: hidden;
  text-overflow: ellipsis;
}

.page-add {
  height: 16rem;
  line-height: 16rem;
  padding: 0;
  font-size: 3rem;
  border: 2px dashed #C0C0C0;
  cursor: pointer;
  color: #C0C0C0;
}

.write-page {
  width: 56.5rem;
  height: 100%;
  margin-left: 4.35rem;
  text-align: left;
}

.write-nav {
  display: flex;
  justify-content: space-between;
  width: 56.5rem;
  margin-top: 3rem;
  font-size: 1.6rem;
  color: #C0C0C0;
}

.run-nav {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  width: 4rem;
}

.run-delete-icon {
  cursor: pointer;
}

.write-input {
  width: calc(100% - 1rem);
  height: 100%;
  padding-left: 1rem;
  border-radius: 8px;
  background-color: #C2E2E8;
}

.save-button, .cancel-button {
  cursor: pointer;
}

.input-title {
  width: 99%;
  height: 5rem;
  font-family: 'Ubuntu Mono', 'Nanum Gothic Coding', monospace;
  font-size: 2rem;
  border: none;
  outline: none;
  background-color: transparent;
}

.textarea-content {
  width: 99%;
  height: calc(100% - 7.35rem);
  font-family: 'Ubuntu Mono', 'Nanum Gothic Coding', monospace;
  font-size: 1rem;
  outline: none;
  border: none;
  resize: none;
  background-color: transparent;
}

.list {
  margin-top: 30px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}